html,
body {
    padding: 0;
    margin: 0;
    font-family: sans-serif;
}

*,
*::after,
*::before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

.wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px 15px;
}


